<template>
    <div id="hd">
        <div class="one">
            <div v-if="agood" @click="$router.go(-1)">
                <i class="el-icon-arrow-left"></i>
            </div>
        </div>
        <div v-if="bgood" class="tow">
            {{bgood}}
        </div>
        <div v-if="cgood" class="sui">
            <div v-show="cgood==1"> <router-link to="/register">注册|登录</router-link> </div>
            <div v-show="cgood==2"> <router-link to="/my"><i class="el-icon-user "></i></router-link> </div>
            <div v-show="cgood==3" @click="bian()" :value='check' >{{zi}}</div>
        </div>
    </div>
</template>

<script>
export default {
    name:"vs",
    props:['agood','bgood','cgood'],
    data(){
        return{
           zi:"编辑",
           check:false
        }
    },
    methods:{
        bian(){
            var check=!this.check
            this.check=check
            if(this.check){
                this.zi='完成'
                console.log(this.zi)
            }else{
                this.zi='编辑'
            }
            this.$emit('chuan',this.zi)
        }
    }
}
</script>

<style>
.one,.tow.sui{
    position: relative;
}
#hd {
    width: 100%;
    height: 60px;
    background: cornflowerblue;
    display: flex;
    line-height: 60px;
    padding-left: 10px;
    padding-right: 10px;
    position: fixed;
    top: 0;
    z-index: 100;
}
.tow{
   width: 50%;
   text-align: center;
   margin-left: 20%; 
   overflow: hidden;
    white-space: nowrap;
    text-overflow:ellipsis;
}
.sui{
    width:25%;
    text-align: right;
}
</style>


