<template>
  <div>
      <div>
        <vs :agood="true" :bgood="bgood" :cgood="false"></vs>
      </div>
      <div id="user">
        <div class="user" v-if="this.check==false"><i class="el-icon-s-custom"></i></div>
        <div class="user" v-if="this.check==true" ><i class="el-icon-user"></i></div>
        <div id="hao">
          <div><b id="bb" @click="skip()">{{account_number}}</b></div>
          <div id="haoo">
            <i class="el-icon-mobile-phone"></i>暂无绑定手机号
          </div>
        </div>
        <div id="biao">
          <i class="el-icon-arrow-right"></i>
        </div>
      </div>
      <div id="condition">
        <ul>
          <li>
            <div><b class="b1">0.00</b>元</div>
            <div class="my" >我的余额</div>
          </li>
          <li>
            <div><b class="b2">0</b>个</div>
            <div class="my">我的优惠</div>
          </li>
          <li>
            <div><b class="b3">0</b>分</div>
            <div class="my">我的积分</div>
          </li>
        </ul>
      </div>
      <div id="member">
        <ul>
            <router-link tag="li" to="/order">
              <div><i class="el-icon-tickets"></i>我的订单</div>
              <div><i class="el-icon-arrow-right"></i></div>
            </router-link>
            
          <li>
             <div><i class="el-icon-s-goods"></i>积分商城</div>
             <div><i class="el-icon-arrow-right"></i></div>
          </li>
          <li>
             <div><i class="el-icon-medal"></i>饿了么会员卡</div>
              <div><i class="el-icon-arrow-right"></i></div>
          </li>
        </ul>
      </div>
      <div id="serve">
        <ul>
          <li>
            <div><i class="el-icon-s-grid"></i>服务中心</div>
            <div><i class="el-icon-arrow-right"></i></div>
          </li>
          <li>
             <div><i class="el-icon-platform-eleme"></i>下载饿了么APP</div>
             <div><i class="el-icon-arrow-right"></i></div>
          </li>
        </ul>
      </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
export default {
    data(){
      return{
        bgood:"我的",
        account_number:'登录/注册',
        check:false
      }
    },
    components: {
        vs
    },
    created(){
        var info = JSON.parse(localStorage.getItem('info'))||[]
        console.log(info)
        if(info.length!=0){
          this.account_number=info.username
        }else{
          this.account_number='登录/注册'
        }
    },
    methods:{
      skip(){
        if(this.account_number=='登录/注册'){
          this.$router.push('/register')
        }else{
          this.$router.push('/quit')
        }
      }
    }
}
</script>

<style>
#member>ul,#serve>ul{
  width: 100%;
}
.el-icon-s-goods{
  color: crimson;
}
.el-icon-medal{
  color: yellow;
}
.el-icon-arrow-right{
  color: #ccc;
}
.el-icon-s-grid{
  color: cornflowerblue;
}
.el-icon-platform-eleme{
  color: cornflowerblue;
  /* font-size: 20px; */
}
#serve,#member{
  padding-top: 10px;
  background: #ccc;
}
#member>ul>li,#serve>ul>li{
  width: 100%;
  display: flex;
  padding-left: 10px;
  height: 40px;
  line-height: 40px;
  background: white;
  border-bottom: 1px solid #ccc; 
  justify-content: space-between;
  padding-right: 10px;
}
.my{
  margin-top: 10px;
  font-size: 14px;
  /* color: #ccc; */
}
  .b1,.b2,.b3{
    font-size: 25px;
  }
  .b1{
    color: orange;
  }
  .b2{
    color: crimson;
  }
  .b3{
    color: yellowgreen;
  }
  #condition{
    width: 100%;
    padding-bottom: 20px;
  }
  #condition>ul{
    width: 100%;
    display: flex;
  }
  #condition>ul>li{
    width: 33.3%;
    text-align: center;
    padding-top: 20px;
  }
  #user{
    width: 100%;
    height: 100px;
    background: cornflowerblue;
    margin-top: 60px;
    display: flex;
    padding-left: 10px;
    padding-top: 10px;
  }
  #user>.user{
    height: 80px;
    width: 80px;
    border-radius: 50%;
    background: white;
    text-align: center;
    line-height: 110px;
  }
  #user>.user>i{
    font-size: 50px;
    color: #ccc;
  }
  #hao{
    width: 70%;
    height: 80px;
    padding-left: 10px;
    padding-top: 10px;
  }
  #haoo{
    padding-top: 10px;
    color: white;
    font-size: 14px;
  }
  #haoo i{
    color: white;
  }
  #bb{
    color: white;
    font-size: 18px;
  }
  #biao{
    height: 100%;
    line-height: 80px;
  }
  #biao>i{
    color: white;
  }
</style>


