<template>
  <div>
      <div>
        <vs :agood="true" :bgood="bgood" :cgood="cgood" v-on:chuan='chuan'></vs>
      </div>
      <div id="hr">
        <hr/>
      </div>
      <div>
        <ul id="site">
          <li v-for="(v,i) in arr" :key="i">
            <div>
              <div>{{v.address}}</div>
              <div>{{v.phone}}</div>
            </div>
            <div class="div1" v-show="check==true">
              <van-icon name="cross" @click="sc(v)" />
            </div>
          </li>
        </ul>
      </div>
      <router-link tag='div' to='/xinzeng'>
          <div id="add">
            <div>新增地址</div>
            <div>
              <i class="el-icon-arrow-right"></i>
            </div>
          </div>
      </router-link>
      
  </div>
</template>

<script>
import vs from '@/components/vs'
import axios from 'axios'
export default {
  components: {
        vs
    },
    data(){
        return{
            bgood:'编辑地址',
            arr:[],
            check:false,
            cgood:3
        }
    },
    created(){
      axios.get('http://elm.cangdu.org/v1/users/38475/addresses').then((res)=>{
        this.arr=res.data
      })
    },
    methods:{
      chuan(data){
        if(data=='完成'){
          this.check=true
        }else{
           this.check=false
        }
      },
      sc(v){
        var lu = 'http://elm.cangdu.org/v1/users/38475/addresses/'+ v.id
        axios.delete(lu).then((res)=>{
          this.check=false
          axios.get('http://elm.cangdu.org/v1/users/38475/addresses').then((res)=>{
            this.arr=res.data
          })
        })
      }
    }
}
</script>

<style>
#hr{
  margin-top: 60px;
  height: 20px;
  padding-top: 10px;
}
#add{
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  line-height: 40px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
#site{
  width: 100%;
}
#site>li{
  height: 60px;
  padding-left: 10px;
  line-height: 20px;
  border-bottom: 1px solid #ccc;
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  padding-right: 10px;
}
#site>li>.div1{
  margin-top: 10px;
}
</style>


