import Vue from 'vue'
import Router from 'vue-router'
import search from '@/search/Search'
import particulars from '@/particulars/Particulars'
import my from '@/particulars/er/my'
import order from '@/particulars/er/order'
import ersearch from '@/particulars/er/search'
import houw from '@/houwPage/HouwPage'
import head  from '@/components/vss'
import register from '@/register/register'
import reset from '@/register/reset'
import take from '@/particulars/er/take'
import quit from '../register/quit.vue'
import modification from '../register/modification.vue'
import site from '../register/site.vue'
import xinzeng from '../register/xinzeng.vue'
import searchAddress from '../register/searchAddress.vue'
import commodity from '../commodity/commodity .vue'
import balance from '../balance/balance.vue'

const home   = () => import('../search/Search.vue')
const Particulars   = () => import('../particulars/Particulars')
const  My  = () => import('../particulars/er/my.vue')
const Order   = () => import('../particulars/er/order.vue')
const Ersearch   = () => import('../particulars/er/search.vue')
const Houw   = () => import('../houwPage/HouwPage.vue')
const Head   = () => import('../components/vss.vue')
const Register = () => import ('../register/register.vue')
const Reset = () => import ('../register/reset.vue')
const Take = () => import ('../particulars/er/take.vue')
const Quit = () => import ('../register/quit.vue')
const Modification = () => import ('../register/modification.vue')
const Site = () => import ('../register/site.vue')
const Xinzeng = () => import ('../register/xinzeng.vue')
const SearchAddress = () => import ('../register/searchAddress.vue')
const Commodity = () => import ('../commodity/commodity .vue')
const Balance = () => import ('../balance/balance.vue')

Vue.use(Router)




export default new Router({
  routes: [
    {
      path:'/head',
      name:"head",
      component:Head 
    },
    {
      path: '/',
      redirect:'/houw'
    },
    {
      path:'/houw',
      component:Houw
    },
    {
      path:'/particulars',
      component:Particulars,
      redirect:'/take',
      children:[
        {
          path:'/ersearch',
          component:Ersearch
        },
        {
          path:'/order',
          component:Order
        },
        {
          path:'/my',
          component:My
        },
        {
          path:'/take',
          component:Take
        }
      ]
    },
    {
      path:'/search',
      component:home
    },
    
    {
      path:'/register',
      component:Register
    },
    {
      path:"/reset",
      component:Reset
    },
    {
      path:'/quit',
      component:Quit
    },
    {
      path:'/modification',
      component:Modification
    },
    {
      path:'/site',
      component:Site
    },
    {
      path:'/xinzeng',
      component:Xinzeng
    },
    {
      path:'/searchAddress',
      component:SearchAddress
    },
    {
      path:'/commodity',
      component:Commodity 
    },
    {
      path:'/balance',
      component:Balance
    }
  ]
})
  


