<template>
  <div>
      <div><vs :agood="true" :bgood="bgood" :cgood="false"></vs> </div>
      <div id="soso">
            <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="add">
            <mt-button type="primary" size="small" @click="sou()">确定</mt-button>
      </div>
      <div id="jing">为了满足商家的送餐要求，建议您从列表中选择地址</div>
      <div>
          <ul>
              
              <li id="llliiii" v-for="(v,i) in arr" :key="i">
                  <router-link tag="div" :to='"/xinzeng?name="+`${v.name}`+"&zuo="+`${v.geohash}`'>
                    <div>{{v.name}}</div>
                    <div>{{v.address}}</div>
                  </router-link>
                  
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
import { Button } from 'mint-ui';
import axios from 'axios'
export default {
    components: {
        vs
    },
    data(){
        return{
            bgood:'搜索地址',
            add:"",
            arr:''
        }
    },
    methods:{
        sou(){
            var lu  = 'http://elm.cangdu.org/v1/pois?type=nearby&keyword='+this.add
            axios.get(lu).then((res)=>{
                this.arr=res.data
                console.log(this.arr)
            })
        }
    }
}
</script>

<style>
#llliiii{
    padding-top: 10px;
    padding-left: 10px;
    line-height: 30px;
    width: 100%;
    border-bottom: 1px solid #ccc;
}
#llliiii>div{
    color: #ccc;
}
#soso{
    margin-top: 60px;
    width: 100%;
    height: 60px;
    padding-top: 10px;
    text-align: center;
}
#soso>input{
    height: 40px;
    width: 75%;
    border-radius: 5px;
    font-size: 18px;
}
#soso>button{
    width: 20%;
    height: 40px;
}
#jing{
    width: 100%;
    height: 20px;
    text-align: center;
    background: beige;
    font-size: 14px;
    color: orange;
}
</style>


