<template>
  <div>
      <div>
        <vs :agood="true" :bgood="bgood" :cgood="false"></vs>
      </div>
      <div id="sousuo">
          <input type="text" placeholder="请输入商家或美食" v-model="zhi"><button @click="sou()">提交查询</button>
      </div>
      <div id="divvvv">
        <ul v-if="check==true">
          <li id="liii" v-for="(v,i) in add" :key="i">
              <div><img :src="'https://elm.cangdu.org/img/'+v.image_path" ></div>
              <div class="div1">
                <div>{{v.name}}</div>
                <div>月售{{v.recent_order_num}}单</div>
                <div>{{v.float_minimum_order_amount}}元起送/距离{{v.distance}}</div>
              </div>
          </li>
        </ul>
        <ul v-if="check==false">
          <li id="iiii"><b>搜索历史</b></li> 
          <li id="llll" v-for="(v,i) in chi" :key="i"><div>{{v}}</div><div><van-icon name="cross" @click="sc(i)" /></div></li>
        </ul>
      </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
import axios from 'axios'
export default {
    data(){
      return{
        bgood:"搜索",
        zhi:'',
        add:[],
        chi:[],
        check:false
      }
    },
    components: {
        vs
    },
    created(){
      this.chi=JSON.parse(localStorage.getItem('chi'))||[]
    },
    watch:{
      zhi:function(v,i){
        if(v.length==0){
          this.check=false
        }
      }
    },
    methods:{
      sou(){
        this.check=true
        var chi = JSON.parse(localStorage.getItem('chi'))||[]
        var index = chi.findIndex((v,i,arr)=>{
          return v==chi
        })
        if(index==-1){
          chi.unshift(this.zhi)
        }
        localStorage.setItem("chi",JSON.stringify(chi))
        console.log(this.$route.query.zuo)
        var lu = 'http://elm.cangdu.org/v4/restaurants?extras[]=restaurant_activity&geohash='+this.$route.query.zuo+'&keyword='+this.zhi+'&type=search'
        axios.get(lu).then((res)=>{
          console.log(res.data)
          this.add=res.data
        })
      },
      sc(i){
        var chi = JSON.parse(localStorage.getItem('chi'))||[]
        chi.splice(i,1)
        this.chi=chi
        localStorage.setItem("chi",JSON.stringify(chi))
      }
    }
}
</script>

<style>
#llll{
  height: 60px;
  width: 100%;
  line-height: 60px;
  padding-left: 20px;
  display: flex;
  padding-right: 20px;
  border-bottom: 1px solid #ccc;
  justify-content: space-between;
}
#iiii{
  height: 60px;
  width: 100%;
  line-height: 60px;
  padding-left: 10px;
  background: gainsboro;
}
#divvvv{
  padding-bottom: 70px;
}
#liii{
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 15px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
#liii img{
  width: 50px;
  height: 50px;
}
#liii>.div1{
  margin-left: 10px;
}
#sousuo{
  width: 100%;
  height: 60px;
  justify-content: space-around;
  display: flex;
  padding-top: 12px;
  margin-top: 60px;
}
#sousuo>input{
  width: 70%;
  height: 60%;
}
#sousuo>button{
  width: 25%;
  height: 60%;
  background: cornflowerblue;
  color: white;
  border: none;
}
</style>


