<template>
  <div>
      <div id="refresh" v-if="sloat">
          <mt-spinner id="mtspinner" type="fading-circle" color="black"></mt-spinner>
      </div>
    <div class="swiper-container">
        <div class="swiper-wrapper" >
            <div class="swiper-slide">
                <ul>
                    <li v-for="(v,i) in arr[0]" :key="i"><img :src="'https://fuss10.elemecdn.com'+v.image_url" alt="123"><p>{{v.title}}</p > </li>
                </ul>
            </div>
            <div class="swiper-slide">
                <ul>
                    <li v-for="(v,i) in arr[1]" :key="i"><img :src="'https://fuss10.elemecdn.com'+v.image_url" alt="123"><p>{{v.title}}</p > </li>
                </ul>
            </div>
        </div>
        <div class="swiper-pagination" id="swiper-pagination" slot="pagination"></div>
    </div>
    <div id="re">
          <i class="el-icon-s-home"></i>附近商家
      </div>
      <ul id="ullll"  v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
              <router-link tag='li' :to="'/commodity?id='+`${v.id}`" id="lie" v-for="(v,i) in add" :key="i">
                    <div id="tu">
                    <img :src="'https://elm.cangdu.org/img/'+v.image_path">
                </div>
                <div id="miao">
                    <div class="div1"><span class="span1">品牌</span><b class="bbb">{{v.name}}</b><span class="span2"><span v-for="(n,m) in v.supports" :key="m">{{n.icon_name}}</span></span></div>
                    <div class="div2"><span id="spannn"><van-rate v-model="v.rating" readonly allow-half void-icon="star"  void-color="#eee" />{{v.rating}}</span><span class="span1">月售{{v.recent_order_num}}单</span><div ><span class="span2">蜂鸟专送</span><span class="span3">准时达</span></div></div>
                    <div class="div3"><span class="span1">￥{{v.float_minimum_order_amount}}起送/配送费约￥{{v.float_delivery_fee}}</span><div><span class="span2">1386公里/</span><span class="span3">14小时47分钟</span></div></div>
                </div>
              </router-link>
              
          <li id="llii">
              <mt-spinner type="triple-bounce" color="rgb(100, 100, 100)"></mt-spinner>
              <div>正在努力加载中...</div>
          </li>
      </ul> 
      
  </div>
</template>

<script>
import Swiper from 'swiper';
import 'swiper/dist/css/swiper.css';    
import axios from 'axios'
export default {
    name: 'take',
  data(){
    return{
      add:[], 
      arr:[],
      sloat:false,
      loading:false,
      b:20
    }
  },
  created(){
      this.sloat=true
     axios.get('http://elm.cangdu.org/v2/index_entry?geohash=39.78493,116.49476&group_type=1&flags[]=F').then((res)=>{ 
            this.arr=this.getStr(res.data)
           this.sloat=false
        })
        this.sloat=true
     axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=31.22299&longitude=121.36025&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=')
        .then((res)=>{
            this.add=res.data
             this.sloat=false
             console.log(this.add)
        })
  },
    mounted(){
        new Swiper ('.swiper-container', {
            loop: true,
            pagination: {
            el: '.swiper-pagination',
            },
        })
    },
    methods:{
        getStr(str) {
            var arr = [];
            for (var i = 0, j = 0; i < str.length; i += 8, j++) {
            arr[j] = str.slice(i, i + 8);
            }
            return arr;
        },
        loadMore() {
        this.loading = true;
        this.b+=20
        setTimeout(() => {
            let last = this.add[this.add.length - 1];
            var lu = 'http://elm.cangdu.org/shopping/restaurants?latitude=31.22299&longitude=121.36025&offset=0&limit='+this.b+'&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]='
            axios.get(lu).then((res)=>{
                this.add=res.data
            })
            this.loading = false;
        }, 2500);
        }
    }
}
</script>

<style>
#spannn{
    font-size: 10px;
    display: flex;
}
.van-rate__icon{
    font-size: 8px;
}
.van-icon-star:before{
    width: 10px;
    font-size: 10px;
}
#llii>div{
    font-size: 12px;
}
#llii{
    text-align: center;
}
.swiper-paginatio-bullet{
    margin-top: 50px !important;
}
html,body{
    width: 100%;
    height: 100%;
}
#modificationbox{
    width: 100%;
    height: 100%;
}
.mint-spinner-fading-circle-circle{
    width: 200%;
    height: 200%;
    left: 600%;
    top: 1000%; 
}
#refresh{
   width: 100%;
   height: 100%;
   position: fixed;
   z-index: 3;
   background: rgba(100,100,100,0.2);
   top: 0;
}
</style>


