<template>
  <div id="tow">
      <div id="tou">
          <div>ele.me</div>
          <div><i class="el-icon-user "></i></div>
      </div>
      <div id="ding">
          <span>当前定位城市:</span>
          <span>{{this.jian.name}}</span>
      </div>
      <div id="di">
          <span class="span1">{{this.jian.name}}</span>
          <span class="span2">></span>
      </div>
      <div id="re">
          热门城市
      </div>
      <ul class="ulll">
          <li v-for="(v,i) in arr" :key="i"><router-link :to="'/search?id='+`${v.id}`">{{v.name}}</router-link></li>
      </ul>
      <div v-for="(v,i) in jia" :key="i">
            <div>{{v}}</div>
            <ul class="ulll">
                <li v-for="(n,m) in info[v]" :key="m">
                    <router-link :to="'/search?id='+`${n.id}`+'&jian='+`${v}`">{{n.name}}</router-link>
                </li>
            </ul>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return {
            arr:[],
            info:[],
            jian:"",
            jia:[],
        }
    },
    created(){
        axios.get('http://elm.cangdu.org/v1/cities?type=hot').then((res)=>{
            this.arr=res.data
        })
        axios.get('http://elm.cangdu.org/v1/cities?type=group').then((res)=>{
            this.info=res.data
            this.jia= Object.keys(res.data).sort()
        })
        axios.get('http://elm.cangdu.org/v1/cities?type=guess2').then((res)=>{
            this.jian=res.data
        })
    }
}
</script>

<style>
#tou{
        width: 100%;
        height: 60px;
        display: flex;
        justify-content: space-between;
        background: cornflowerblue;
        font-size: 20px;
        color: white;
        line-height: 60px;
        padding-left: 10px;
        padding-right: 10px;
    }
#tow{
    background: #ccc;
}
#ding{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    line-height: 60px;
    padding-left: 10px;
    padding-right: 10px;
    border: 1px solid #ccc;
    background: white;
}
#di{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    font-size: 25px;
    padding-left: 10px;
    padding-right: 10px;
    line-height: 60px;
    background: white;
}
.span1{
    color: cornflowerblue;
}
.span2{
    color: #ccc;
}
#re{
    margin-top: 15px;
    width: 100%;
    height: 40px;
    background: white;
    line-height: 40px;
    padding-left: 10px;
    border-bottom: 1px solid #ccc;
}

.re{
    color: cornflowerblue;
}
.ulll{
    background: white;
    display: flex;
    flex-wrap: wrap;
}
.ulll>li{
    width: 24.3%;
    text-align: center;
    height: 40px;
    line-height: 40px;
    font-size: 18PX;
    border: 1px solid #ccc;
    overflow: hidden;
    white-space: nowrap;
    text-overflow:ellipsis;
}
</style>


