<template>
  <div>
      <div><vs :agood="true" :bgood="bgood" :cgood="false"></vs> </div>
      <div class="mi" id="mima">
            <input type="text" placeholder="账号" v-model="username" >
      </div>
      <div class="zhang">
            <input type="text" placeholder="旧密码" v-model="password" id="inputmima"/>
      </div>
      <div class="zhang">
            <input type="text" placeholder="请输入新密码" v-model="confirmpassword" />
      </div>
      <div class="zhang">
            <input type="text" placeholder="请确认密码" v-model="newpassword" />
      </div>
      <div class="yan">
                <div  class="login-code">
                     <input type="text" v-model="captcha_code">
                </div>
                <div id="zi">
                    <span><img :src="this.luelue" /></span>
                    <span @click="huan()">
                        <div>看不清？</div>
                        <div>换一张</div>
                    </span>
                </div>
        </div>  
        <div id="button">
            <el-button type="success" id="but" @click="confirm()">确认修改</el-button>
        </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
import axios from 'axios'
import { MessageBox } from 'mint-ui';
export default {
    components: {
        vs
    },
    data(){
        return{
            bgood:'重置密码',
            luelue:"",
            captcha_code:"",
            password:"",
            username:'', 
            confirmpassword:"",
            newpassword:''
        }
    },
    created(){
         this.huan()
    },
    methods:{
         huan(){
            axios.post('http://elm.cangdu.org/v1/captchas').then((res)=>{
                this.luelue=res.data.code
            })
         },
         confirm(){
             axios.post('http://elm.cangdu.org/v2/changepassword',{
                captcha_code:this.captcha_code,oldpassWord:this.password,username:this.username,confirmpassword:this.confirmpassword,newpassword:this.newpassword
             }).then((res)=>{
                 console.log(res.data)
                 if(res.data.status==1){
                    MessageBox('', '密码修改成功').then(action => {
                        var info = JSON.parse(localStorage.getItem(info))||[]
                        info=[]
                        localStorage.setItem('info',JSON.stringify(info))
                        this.$router.push('/register')
                    })
                 }else if(res.data.status==0){
                     if(res.data.message=='密码不正确'){
                         MessageBox('', '密码输入错误');
                     }else if(res.data.message=='两次密码不一致'){
                         MessageBox('', '两次密码输入不一致');
                     }else if(res.data.message=='验证码不正确'){
                         MessageBox('', '验证码输入错误');
                     }
                 }
             })
         }
    }
}
</script>

<style>
    .mi{
        margin-top: 80px;
        height: 60px;
        width: 100%;
        border-bottom: 2px solid #ccc;
        border-top: 2px solid #ccc;
    }
    .mi>input{
        width: 100%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
    }
    #mima{
        margin-top: 70px;
    }
    .yan>input{
        width:100%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
    }
    .yan,.zhang{
        display: flex;
    }
    .zhang,.yan{
        height: 60px;
        width: 100%;
        border-bottom: 2px solid #ccc;
    }
    .zhang>input{
        width: 90%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
    }
    .login-code{
        width: 45%;
    }
    .login-code>input{
        width: 100%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
    }
     #zi{
        display: flex;
        line-height: 20px;
        
    }
    #zi img{
        height: 60px;
    }
    #zi>span>div{
        font-size: 10px;
    }
        #but{
        width: 90%;
        color: white;
    }
    #button{
        width: 100%;
        margin-top: 20px;
        text-align: center;
    }
</style>


