<template>
  <div id="sui">
      <div>
          <vs :agood="true" :bgood="bgood" :cgood="cgood"></vs>
      </div>
        <div id="xiang">
            <router-view></router-view>
        </div>
       <div id="hahah">
          <ul id="zuidi">
              <li><i class="el-icon-potato-strips"></i><router-link to="/take"><span>外卖</span> </router-link></li>
              <li> <i class="el-icon-discover"></i> <router-link :to="'/ersearch?zuo='+`${this.zhi}`"><span>搜索</span> </router-link> </li>
              <li> <i class="el-icon-tickets"></i> <router-link to="/order"><span>订单</span></router-link> </li>
              <li> <i class="el-icon-user"></i> <router-link to="/my"><span>我的</span></router-link> </li>
          </ul>
      </div>
      
  </div>
</template>

<script>

import vs from '@/components/vs'
import axios from 'axios'
export default {
   data(){
       return{
           bgood:"",
           zhi:'',
           cgood:''
       }
   },
   components: {
    vs
  },
   created(){
       this.zhi=this.$route.query.zuo
    //    console.log(this.zhi)
       this.bgood=this.$route.query.name||111
    //    console.log(this.bgood)
    var info = JSON.parse(localStorage.getItem('info'))||[]
      console.log(info)
      if(info.length!=0){
        this.cgood='2'
      }else{
        this.cgood='1'
      }
   }

}
</script>

<style>

#xiang{
    margin-top: 60px;
}
#name{
    width: 50%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow:ellipsis;
}
#hahah{
    position: fixed;
    z-index: 100;
    bottom: 0px;
    width: 100%;
    background: white;
    padding: 5px;
}
#zuidi li i,#zuidi li span{
    display: block;
}
#zuidi li{
    padding-top: 10px;
    width: 25%;
    height: 60px;
    text-align: center;
    line-height: 40px;

}
#zuidi{
    display: flex;
}

#ullll{
    border: 1px solid #ccc;
    width: 100%;
    margin-bottom: 80px;
}
.bbb{
    display: block;
    width: 100px;
    overflow: hidden; 
    white-space: nowrap;
    text-overflow:ellipsis;
}
#miao .div3 .span3{
    font-size: 12px;
    color: cornflowerblue;
}
#miao .div3 .span2{
    font-size: 12px;
    color: #ccc;
}
    #miao .div3 .span1{
        font-size: 12px;
        color: #ccc;
    }
    #miao{
        padding-left: 10px;
        width: 100%;
    }
    #miao .div2,#miao .div3{
         margin-top: 10px;
    }
    #miao .div1,#miao .div2,#miao .div3{
        display: flex;
        justify-content: space-between;
    }
    #miao .div1 .span1{
        font-size: 12px;
        background: yellow;
        padding-left: 5px;
        padding-right: 5px;
    }
    #miao .div1 .span2 span{
        font-size: 12px;
        color: #ccc;
    }
    #miao .div2 .span1{
        font-size: 12px;
    }
    #miao .div2 .span2{
        font-size: 12px;
        background: cornflowerblue;
        color: white;
    }
    #miao .div2 .span3{
        margin-left: 5px;
        border: 2px solid cornflowerblue;
        color: cornflowerblue;
        box-sizing: border-box;
    }
    #tou{
        width: 100%;
        height: 60px;
        display: flex;
        justify-content: space-between;
        background: cornflowerblue;
        font-size: 20px;
        color: white;
        line-height: 60px;
        padding-left: 10px;
        padding-right: 10px;
    }
    #re{
        margin-top: 15px;
        width: 100%;
        height: 40px;
        background: white;
        line-height: 40px;
        padding-left: 10px;
        border-bottom: 1px solid #ccc;
    }
    img{
        width: 80px;
        height: 80px;
    }
    #lie{
        padding-left: 10px;
        padding-top: 10px;
        display: flex;
        width: 100%;
    }
    .swiper-container{
        width: 100%;
        height: 220px;
        padding: 1px 0px;
        background:white;
    }
    .swiper-container ul{
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        line-height: 30px;
    }
     .swiper-container img{
        width: 80px;
        display: block;
        margin: 0 auto;
    }
    .swiper-container li{
        text-align: center;
        width: 25%;
        font-size: 16px;
    }
</style>


