<template>
  <div>
        <div>
          <vs :agood="true" :bgood="bgood" :cgood="false"></vs> 
        </div>
        <div id="kong">    </div>
        <div id="xinzeng">
          <ul>
            <li><input type="text" placeholder="请填写你的姓名" v-model="name" /></li>
            <router-link tag="li" to="/searchAddress"><input type="text" placeholder="小区/写字楼/学校等" v-model="address_detail" /></router-link>
            <li><input type="text" placeholder="请填写详细送餐地址" v-model="address" /></li>
            <li><input type="text" placeholder="请填写能够联系到您的手机号" v-model="phone" /></li>
            <li><input type="text" placeholder="备用联系电话（选填）" v-model="phone_bk" /></li>
          </ul>
        </div>
        <div id="xinzengbut">
          <mt-button type="default" id="mtbut" size="large" @click="add()">新增地址</mt-button>
        </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
import axios from 'axios'
export default {
  components: {
        vs
    },
    data(){
        return{
            bgood:'新增地址',
            address:'',
            address_detail:"",
            name:'',
            phone:'',
            phone_bk:''
        }
    },
    created(){
      this.address_detail=this.$route.query.name
    },
    methods:{
      add(){
        
        var zuo=this.$route.query.zuo
        axios.post('http://elm.cangdu.org/v1/users/38475/addresses',{
          address:this.address,address_detail:this.address_detail,geohash:zuo,name:this.name,phone:this.phone,phone_bk:this.phone_bk,
          poi_type:0,sex:1,tag:'公司',tag_type:4
        }).then((res)=>{
          this.$router.push('/site')
        })
        console.log(name,zuo)
      }
    }
}
</script>

<style>
  #xinzeng{
    margin-top: 60px;
  }
  #kong{
    width: 100%;
    height: 10px;
  }
  #xinzeng>ul>li{
    width: 100%;
    height: 50px;
    padding-top: 10px;
    text-align: center;
    padding-bottom: 10px;
  }
  #xinzeng>ul>li>input{
    width: 90%;
    height: 100%;
    background: #ccc;
    border-radius: 5px;
  }
  #xinzengbut{
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 20px;
  }
  #mtbut{
    background: yellowgreen;
  }
</style>


