<template>
  <div>
      <div>
        <vs :agood="true" :bgood="bgood" :cgood="cgood"></vs>
      </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
export default {
    data(){
      return{
        bgood:"订单列表",
        cgood:''
      }
    },
    created(){
      var info = JSON.parse(localStorage.getItem('info'))
      console.log(info)
      if(info.length!=0){
        this.cgood='2'
      }else{
        this.cgood='1'
      }

    },
    components: {
        vs
      }
}
</script>

<style>

</style>


