<template>
  <div>
      <div>
          <vs :agood="true" :bgood="bgood" :cgood="false"></vs>
      </div>
      <div id="quit">
          <ul>
              <div><input type="file" id="file" @change="chuan()" ref='inp'></div>
              <li class="li1">
                  
                  <div>头像</div>
                  <div id="headPortrait">
                      <div class="user">
                          <img v-if="imga" id="imgg" :src="'https://elm.cangdu.org/img/'+imga">
                          <i v-else class="el-icon-s-custom"></i>
                      </div>
                      <div><i class="el-icon-arrow-right"></i></div>
                  </div>
                  
              </li>
              <router-link tag='li' to='/modification'>
                <!-- <li> -->
                    <div>用户名</div>
                    <div>13105680572<i class="el-icon-arrow-right"></i></div>
                <!-- </li> -->
              </router-link>
              <router-link tag='li' to='/site'>
                <!-- <li> -->
                    <div>收货地址</div>
                  <div><i class="el-icon-arrow-right"></i></div>
                <!-- </li> -->
              </router-link>
          </ul>
      </div>
      <div class="binding">
          账号绑定
      </div>
      <div class="cellphone" @click="cellphone()">
          <span><i class="el-icon-mobile-phone"></i>手机</span>
            <span><i class="el-icon-arrow-right"></i></span>
      </div>
      <div class="binding">
          安全设置
      </div>
      <router-link tag='div' to="/reset">
        <div class="cellphone">
          <span>登录密码</span>
          <span>修改<i class="el-icon-arrow-right"></i></span>
        </div>
      </router-link>
      
      <div id="logOut">
          <mt-button id="logout" type="danger" size="large" @click="logOut()">退出登录</mt-button>
      </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
import { Button,MessageBox  } from 'mint-ui';
import axios from 'axios'
export default {
    components: {
        vs
    },
    data(){
        return{
            bgood:'账户信息',
            imga:''
        }
    },
    created(){
        var img = JSON.parse(localStorage.getItem('img'))||[]
        this.imga=img[0]
    },
    methods:{
        logOut(){
            MessageBox.confirm('', { 
                 message: '是否退出登录', 
                 confirmButtonText: '退出登录', 
                 cancelButtonText: '在等等' 
                 }).then(action => { 
                 if (action == 'confirm') {     //确认的回调
                    var info = JSON.parse(localStorage.getItem(info))||[]
                    info=[]
                    localStorage.setItem('info',JSON.stringify(info))
                    this.$router.push('/my') 
                 }
                 })           
        },
        cellphone(){
             MessageBox('', '请在手机App中设置 ');  
        },
        chuan(){
            var info = JSON.parse(localStorage.getItem('info'))
            let input = this.$refs.inp;
            var img = JSON.parse(localStorage.getItem(img))
            img=[]
            localStorage.setItem('img',JSON.stringify(img))
            let data = new FormData()
            data.append('file',input.files[0])
            axios.post('http://elm.cangdu.org/eus/v1/users/'+info.id+'/avatar',data,
            {headers:{"Content-Type":"multipart/from-data"}})
            .then((res)=>{
                var img = JSON.parse(localStorage.getItem(img))||[]
                img.push(res.data.image_path)
                console.log(img[0])
                this.imga = img[0]
                localStorage.setItem('img',JSON.stringify(img))
            })
        }
    }
}
</script>

<style>
#imgg{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
#file{
    width:100%;
    height: 80px;
    position: absolute;
    z-index: 10;
    opacity: 0;
    /* background: none; */
}
#logOut{
    width: 100%;
    padding-top: 30px;
    padding-left: 10px;
    padding-right: 10px;
}
.cellphone{
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 40px;
    padding-left: 10px;
    padding-right: 10px;
    line-height: 40px;
    border-bottom: 1px solid #ccc;
}
.binding{
    width: 100%;
    font-size: 12px;
    background: #ccc;
    height:30px;
    line-height: 30px;
    padding-left: 10px;
}
#quit{
    margin-top: 70px;
    width: 100%;
    border-top: 1px solid #ccc;
}
#quit>ul{
    width: 100%;
}
#quit>ul>.li1{
    height: 95px;
    line-height: 95px;
    position: relative;
}
#quit>ul>li{
    padding: 10px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
}
#headPortrait{
     display: flex;
}
#headPortrait>.user{
    height: 80px;
    width: 80px;
    border-radius: 50%;
    background: cornflowerblue;
    text-align: center;
    line-height: 110px;
}
#headPortrait>.user>i{
    font-size: 50px;
    color:#ccc;
  }
</style>


