<template>
    <div id="one">
        <div>
            <vs :agood="true" :bgood="bgood" :cgood="cgood"></vs>
        </div>
        <div id="shen">
            <div>
                <input type="text" placeholder="输入学校、商务楼、地址" v-model="price">
            </div>
            <div>
                <el-button type="primary" @click="sou()">提交</el-button>
            </div>
        </div> 
        <div class="wei" :value="this.check" v-show="check==true">
            <div id="li">
                搜索历史
            </div>
            <ul>
                <li v-for="(v,i) in arr" :key="i">
                    <router-link :to="'/particulars?name='+`${v.name}`+'&zuo='+`${v.geohash}`" tag="div"> 
                         <div id="da">
                            {{v.name}}
                        </div>
                        <div id="xiao">
                            {{v.address}}
                        </div>
                    </router-link>
                </li>
            </ul>
            <div id="qing" @click="qing()">
                清除所有
            </div>
        </div>
        <div class="wei" :value="this.check" v-show="check==false">
            <ul>
                <li v-for="(v,i) in local" :key="i" @click="qu(v)">
                    <router-link :to="'/particulars?name='+`${v.name}`+'&zuo='+`${v.geohash}`" tag="div"> 
                         <div id="da">
                            {{v.name}}
                        </div>
                        <div id="xiao">
                            {{v.address}}
                        </div>
                    </router-link>
                   
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import vs from '@/components/vs'
export default {
    components: {
        vs
    },
    data(){
        return{
            bgood:'',
            price:"",
            local:[],
            arr:[],
            check:true,
            cgood:''
        }
    },
    created(){
      var info = JSON.parse(localStorage.getItem('info'))
      console.log(info)
      if(info.length!=0){
        this.cgood='2'
      }else{
        this.account_number='1'
      }

    },
    mounted(){
        var zhi=this.$route.query.id
        var ok='http://elm.cangdu.org/v1/cities/'+zhi
        axios.get(ok).then((res)=>{
            this.bgood=res.data.name
        })
        var arr = JSON.parse(localStorage.getItem("data"))||[]
        this.arr=arr
    },
    methods:{
        sou(){
             this.check=false
            var lu = 'http://elm.cangdu.org/v1/pois?type=search&city_id='+`${this.$route.query.id}`+'&keyword='+`${this.price}`
            axios.get(lu)
            .then((res)=>{
                this.local=res.data
                console.log(this.local)
            })
        },
        qu(v){
            console.log(v)
            this.price=v.name
            this.local=[]
            this.check=true
            var arr = JSON.parse(localStorage.getItem("data"))||[]
            var obj = {name:v.name,address:v.address}
            var index = arr.findIndex((item,i,arr)=>{
                return item.name==v.name
            })
            if(index==-1){
                arr.unshift(obj)
                this.arr=arr
            }
            localStorage.setItem("data",JSON.stringify(arr))
        },
        qing(){
            var arr = JSON.parse(localStorage.getItem("data"))||[]
            arr=[]
            this.arr=arr
            localStorage.setItem("data",JSON.stringify(arr))
        }
    }
}
</script>

<style>
    #tou{
        width: 100%;
        height: 60px;
        display: flex;
        justify-content: space-between;
        background: cornflowerblue;
        font-size: 20px;
        color: white;
        line-height: 60px;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 
    }
    #shen{
        width: 100%;
        height: 100px;
        border-top: 1px solid #ccc;
        margin-top: 10px;
        border-bottom: 1px solid #ccc;
        text-align: center;
        margin-top: 60px;
    }
    #shen input{
        margin-top: 10px;
        width: 90%;
        height: 30px;
        font-size: 20px;
    }
    #shen button{
        margin-top: 10px;
        width: 90%;
    }
    #li{
        width: 100%;
        background: #ccc;
    }
    .wei,.wei>ul{
        width: 100%;
    }
    .wei>ul>li{
        width: 100%;
        height: 80px;
        padding-left: 15px;
        padding-top: 20px;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
    }
    #da{
        width: 100%;
        height: 50%;
        font-size: 20px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow:ellipsis;
    }
    #xiao{
        width: 100%;
        color: #ccc;
        overflow: hidden;
        white-space: nowrap;
        text-overflow:ellipsis;
    }
    #qing{
        width: 100%;
        height: 60px;
        text-align: center;
        line-height: 60px;
        font-size: 25px;
    }
</style>


