<template>
  <div id="jiaaaa">
      <ul class="ul1">
          <li v-for="(v,i) in jian" :key="i" @click="gun(i)"  >
                  {{arr[v].name}}
          </li>
      </ul>
      <ul class="ul2" ref="ok">
          <li v-for="(v,i) in jian" :key="i" class="neu">
              <div id="ddiivv">
                    <h1>{{arr[v].name}}</h1>
                    <h3>{{arr[v].description}}</h3>
              </div>
             <ul>
                  <li class="liil1" v-for="(item,key) in arr[v].foods" :key="key" >
                      <div class="divabc">
                          <img :src="'https://elm.cangdu.org/img/'+item.image_path">
                      </div>
                      <div class="divcba">
                          <h2>{{item.name}}</h2>
                          <div id="intro">{{item.description}}</div>
                          <div id="volume"><span>月售出{{item.month_sales}}份</span><span>好评{{item.satisfy_rate}}</span></div>

                          <div id="price">
                              <span>￥<b>{{item.specfoods[0].price}}起</b></span>
                              <div class="span2" v-if="check==false" @click="newBut($event,item)"><van-icon name="plus" id="plus" /></div>
                              <div class="div1" v-else>
                                  <button>-</button>
                                  1
                                  <button  @click="newBall($event,v)">+</button>
                              </div>
                          </div>
                      </div>
                  </li>
              </ul>
          </li>
      </ul>
  </div>
</template>

<script>
import axios from 'axios'
import jq from 'jquery'
// import '@/theball/theBall.css'
export default {
     data(){
        return{
            jian:[],
            arr:[],
            list:[],
            check:false
        }
    },
    created(){
        var lu = 'http://elm.cangdu.org/shopping/v2/menu?restaurant_id='+this.$route.query.id
        axios.get(lu).then((res)=>{   
            this.arr=res.data
            // console.log(this.arr)
            this.jian=Object.keys(res.data)
        })
        
    },
    methods:{
        gun(o){
            let neu = document.getElementsByClassName('neu')
            let height = 0;
            this.list.push(height)
            for(var  i = 0; i<neu.length;i++){
                height+=neu[i].clientHeight
                this.list.push(height)
            }
           jq(this.$refs.ok).animate({scrollTop:this.list[o]},500)
        },
        newBall(e){
            let div=document.createElement('div');
            div.className='ballFather';
            document.body.appendChild(div);
            let sonDiv=document.createElement('div');
            sonDiv.className='ballSon';
            div.appendChild(sonDiv);
            // console.log(event.target.getBoundingClientRect().top);
            let top=event.clientY;//获得 跳动小球top
            let left=event.clientX;//获得 left
            div.style.left=left+'px';
            div.style.top=top+'px';
            let y = (window.innerHeight - top);// 下落的高度
            div.style.webkitTransform = `translateY(${y}px)`;
            div.style.transform = `translateY(${y}px)`;
            sonDiv.style.webkitTransform = `translateX(-${left-10}px)`;
            sonDiv.style.transform = `translateX(-${left-10}px)`;
            //保证 小球运动结束后，调用购物车的动画
            setTimeout(()=>{
                // document.querySelector('.sc').classList 找到class sc 的元素  添加一个动画 
                document.querySelector('.sc').classList.add("animation");
                setTimeout(()=>{
                    document.querySelector('.sc').classList.remove("animation");
                },800)
            },900)
        },
        newBut(e,v){
            // this.check=true
            this.newBall(e)
            var arr = JSON.parse(localStorage.getItem("arr"))||[]
            var obj ={id:v.specfoods[0].item_id,name:v.name,price:v.specfoods[0].price,num:1,check:false}
            var index = arr.findIndex((item,key,arr)=>{
                return item.id==v.specfoods[0].item_id
            })
            if(index==-1){
                arr.unshift(obj)
            }else{
                arr[index].num+=1
            }
             this.$emit('crea',arr)
            localStorage.setItem("arr",JSON.stringify(arr))
        },     
   }



}
</script>

<style>
    #plus{
        color: white;
    }
    #price .span2{
        background: cornflowerblue;
        border-radius: 50%;
        display: flex;
        width: 8%;
        height:8%;
        margin-left: 100px;
        
    }
    #price{
        width: 100%;
        display: flex;
        /* justify-content: space-between; */
    }
    #volume span{
        font-size: 12px;
        padding-left: 10px;
    }
    #intro{
        font-size: 12px;
        color: #ccc;
    }
    #jiaaaa{
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        /* position: relative; */
       padding-top: 60px;
    }
    #jiaaaa .ul1{
        width: 25%; 
        height: 100%;
        overflow-y: auto;
        position: fixed;
        padding-bottom: 60px;
    }
     #jiaaaa .ul1 li{
        height: 60px;
        overflow: hidden;
        line-height: 60px;
        background: azure;
        white-space: nowrap;
        text-overflow:ellipsis;
        text-align: center;
        border-bottom: 1px solid #ccc;
        
     }
     #jiaaaa .ul2{
        padding: 2%;
        height: 1000px;
        margin-left: 25%;
        overflow-y: auto;
        z-index: 1;
        padding-bottom: 60px;
     }
     #ddiivv{
         display: flex;
         width: 100%;
         height: 40px;
         line-height: 40px;
     }
     .liil1{
         width: 100%;
         display: flex;
     }
     #ddiivv h3{
         font-size:70%;
         padding: 2px;
     }
     .divabc img{
         width: 45px;
         height: 45px;
     }
     .divcba{
         width: 75%;
         padding-left: 10px;
         line-height: 30px;
         padding-bottom: 10px;
     }
     #price>.div1{
         display: flex;
         margin-left: 100px;
     }
     #price>.div1>button{
         width: 30px;
         height: 30px;
         border-radius: 50%;
         border: none;
         background: cornflowerblue;
         color: white;
     }
    
</style>


