<template>
    <div id="register">
        <div>
            <vs :agood="true" :bgood="bgood" :cgood="false"></vs>
        </div>
        <div class="mi">
            <input type="text" placeholder="账号" v-model="username">
        </div>
        <div class="zhang">
            <input type="password"  placeholder="密码" id="inputmima" v-model="password">
            <mt-switch v-model="value" @click="xian()"></mt-switch>
        </div>
        <div class="yan">
                <div  class="login-code">
                     <input type="text" v-model="captcha_code">
                </div>
                <div id="zi">
                    <span><img :src="this.luelue" /></span>
                    <span @click="huan()">
                        <div>看不清？</div>
                        <div>换一张</div>
                    </span>
                </div>
        </div>         
        <div id="hint">
            <div class="div1">温馨提示:未注册过的账号,登录时将自动注册</div>
            <div>注册过的用户可凭账号和密码登录</div>
        </div>
        <div id="button">
            <el-button type="success" id="but" @click="register()">登录</el-button>
        </div>
        <div id="reset">
            <router-link to="/Reset">重置密码？</router-link>
        </div>
    </div>
</template>

<script> 
import axios from 'axios'
import vs from '@/components/vs'
import { MessageBox } from 'mint-ui';
axios.defaults.withCredentials=true;
export default {
    components: {
        vs
    },
    data(){
        return{
            bgood:'密码登录',
            luelue:"",
            value:false,
            captcha_code:"",
            password:"",
            username:'',    
        }
    },
    created(){
        this.huan()
    },
     methods:{
         huan(){
            axios.post('http://elm.cangdu.org/v1/captchas').then((res)=>{
                this.luelue=res.data.code
            })
         },
         fang(){
             axios.post('http://elm.cangdu.org/v2/login',{
                 captcha_code:this.captcha_code,password:this.password,username:this.username
             }).then((res)=>{
                 console.log(res)
             })
         },
         register(){
             if(this.username==""){
                 this.huan()
                 MessageBox('', '用户名不能为空');
                 
             }
             if(this.password==''){
                 this.huan()
                  MessageBox('', '密码不能为空');
                  
             }else{
                axios.post('http://elm.cangdu.org/v2/login',{
                captcha_code:this.captcha_code,password:this.password,username:this.username
                }).then((res)=>{
                    if(res.data.message=='密码错误'){
                        MessageBox('', '密码错误');
                    }
                })
             }
             var info = JSON.parse(localStorage.getItem(info))||[]
             info=[]
             localStorage.setItem('info',JSON.stringify(info))
             axios.post('http://elm.cangdu.org/v2/login',{
                captcha_code:this.captcha_code,password:this.password,username:this.username
                }).then((res)=>{
                    console.log(res)
                    if(this.captcha_code==''){
                        MessageBox('', '验证码不能为空');
                    }else if(res.data.message=='验证码不正确'){
                        MessageBox('', '验证码输入不正确');
                    }
                    if(res.data.username){
                        var info = JSON.parse(localStorage.getItem(info))||[]
                        var obj = res.data
                        localStorage.setItem('info',JSON.stringify(obj))
                        this.$router.push('/my')
                    }   
                })
         }
     }
}
    </script>

    <style>
    .login-code{
        width: 68%;
    }
    .login-code>input{
        width: 100%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
    }
    #reset{
        width: 90%;
        text-align: right;
        margin: 20px auto;
        color: cornflowerblue;
    }
    #but{
        width: 90%;
        color: white;
    }
    #button{
        width: 100%;
        text-align: center;
    }
    #hint{
        width: 100%;
        height: 50px;
        padding-left: 20px;
    }
    #hint>div{
        color: red;
        font-size: 12px;
    }
    #hint>.div1{
        height: 30px;
    }
    #zi{
        display: flex;
        line-height: 20px;
        
    }
    #zi img{
        height: 60px;
    }
    #zi>span>div{
        font-size: 10px;
    }
    .yan>input{
        width:100%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
        /* background: wheat; */
    }
    .yan,.zhang{
        display: flex;
    }
    .zhang,.yan{
        height: 60px;
        width: 100%;
        border-bottom: 2px solid #ccc;
    }
    .mi{
        margin-top: 80px;
        height: 60px;
        width: 100%;
        border-bottom: 2px solid #ccc;
        border-top: 2px solid #ccc;
    }
    .mi>input{
        width: 100%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
    }
    .zhang>input{
        width: 90%;
        height: 100%;
        padding-left: 20px;
        border:none;
        font-size: 18px;
    }
</style>


