<template>
  <div id="modificationbox">
      <div>
          <vs :agood="true" :bgood="bgood" :cgood="false"></vs>
      </div>
      <div id="modification">
          <div class="modification1">
              <input type="text" placeholder="输入用户名" v-model="arr" @change="huan()">  
          </div>
          <div id="modification2">
                {{aaa}}
          </div>
      </div>
      <div class="modification3">
          <mt-button type="primary" size="large">确认修改</mt-button>
      </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
import { Button,InfiniteScroll  } from 'mint-ui';
export default {
    components: {
        vs
    },
    data(){
        return{
            bgood:'修改用户名',
            arr:'',
            aaa:"用户只能修改一次(5-24字符之间)",
            
        }
    },
    methods:{
        huan(){
            let modification2 = document.getElementById('modification2')
            if(this.arr.length<5){
                this.aaa='用户名长度在5到24之间';
                // modification2.style.color='red'
            }
            console.log(modification2)
        },
        
    },
}
</script>

<style>

.modification3{
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
}
#modification2{
    width: 100%;
    height: 50px;
    font-size: 10px;
    padding-left: 10px;
    padding-top: 10px;
}
.modification1{
    width: 100%;
    text-align: center;
    padding-top: 10px;
}
.modification1>input{
    width: 95%;
    height: 50px;
    padding-left: 10px;
    font-size: 20px;
}
#modification{
    width: 100%;
    margin-top: 60px;
}
</style>


