<template>
  <div>
        <div>
            <vs :agood="true" :bgood="bgood" :cgood="cgood"></vs>
        </div>
        <div id="sitee">
            <div>
                <span><van-icon name="location-o" /></span>
                <span>请添加一个收货地址</span>
            </div>
            <div>
                <van-icon name="arrow" />
            </div>
        </div>
        <div id="tiem">
            <div class="tiem"><b>送达时间</b></div>
            <div class="tiem2">
                <div class="div1">尽快送达|预计17:14</div>
                <div class="div2">蜂鸟专送</div>
            </div>
        </div>
        <div>
            <ul class="woat">
                <li>
                    <div>支付方式</div>
                    <div>在线支付<van-icon id="vanicon" name="arrow" /></div>
                </li>
                <li>
                    <div>红包</div>
                    <div>暂时只在饿了么APP中支持</div>
                </li>
            </ul>
        </div>
  </div>
</template>

<script>
import vs from '@/components/vs'
export default {
    components: {
        vs
    },
    data(){
        return{
            bgood:'确认订单'
        }
    },
    created(){
    var info = JSON.parse(localStorage.getItem('info'))||[]
      if(info.length!=0){
        this.cgood='2'
      }else{
        this.cgood='1'
      }
   }
}
</script>

<style>
#sitee{
    margin-top: 60px;
    width: 100%;
    height: 80px;
    display: flex;
    padding-left: 10px;
    padding-right: 10px;
    justify-content: space-between;
    line-height: 80px;
    border-bottom: 2px dashed #ccc;
}
#tiem{
    width: 100%;
    height: 100px;
    display: flex;
    margin-top: 10px;
    border-left: 5px solid cornflowerblue;
    padding-left: 20px;
    padding-right: 20px;
    justify-content: space-between;
    line-height: 50px;
}
#tiem>.tiem{
    margin-top: 25px;
}
#tiem>.tiem2{
    text-align: right;
}
#tiem>.tiem2>.div1{
    color: cornflowerblue;
}
#tiem>.tiem2>.div2{
    font-size: 14px;
    display: inline;
    color: white;
    padding: 5px;
    border-radius: 5px;
    background: cornflowerblue;
}
.woat{
    width: 100%;
    margin-top: 10px;
}
.woat>li{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
    height: 30px;
    line-height: 30px;  
}
.woat>li>div{
    color: #ccc;
    font-size: 12px;
}
.woat>li>div>#vanicon{
    color: #ccc;
}
</style>


